@use 'Library';

.slick-prev:before,
.slick-next:before {
  color: Library.$color1!important;
  text-shadow: 0px 0px 5px rgba(0, 0, 0, 1);
}
.servicessectionheader{
    font-family: Library.$main-header-font;
    font-weight: 600;
    text-align: center;
    font-size: 32px;
    padding-top: 15px;
    padding-bottom: 30px;
}

// .slick-track div{
//     display: flex;
//     flex-direction: row;
//     justify-content:center;
//     margin:auto;
//     padding-top: 5px;
    
// }
.servicesliderdiv{
    max-width: 1100px;
    margin: 10px;
    padding: 10px;
    margin: auto;
    padding-right: 0;
}
.servicediv{
    padding-right: 200px;
    margin: auto;
    padding-bottom: 50px;;

}
.serviceslider{
    padding-right: 0px;
}
.serviceheader{
    margin: auto;
    text-align: center;
    font-size: 16px;
    
    padding: 10px;
    font-family: Library.$main-header-font;
}
.servicedesc{
    font-size: 16px;
    margin: auto;
    text-wrap: normal;
    max-width: 300px;
    text-align: left;
    font-family: Library.$text-font;
}
.serviceimg{
    max-width: 250px;
    border-radius: 10px;
    margin:auto;
}
@media only screen and (max-width: 480px) {
    .servicessectionheader{
        font-size: 18px;
    }
    .servicedesc{
        text-align: center;
    }
  }
  @media only screen and (max-width: 380px) {
  .serviceimg{
    max-width: 180px;
  }
  .servicedesc{
    font-size: 16px;
    max-width: 300px;
    text-align: center;
    padding-right: 20px;
    padding-left: 20px;
  }
  .serviceheader{
    font-size: 16px;

  }
}